import React from 'react';
import Jumbotron from 'react-bootstrap/lib/Jumbotron';
import Button from 'react-bootstrap/lib/Button';

import Grid from 'react-bootstrap/lib/Grid';
import Row from 'react-bootstrap/lib/Row';
import Col from 'react-bootstrap/lib/Col';

import TopNavBar from '../components/TopNavBar';
import LoginFormModule from '../components/LoginFormModule';
import SignUpModule from '../components/SignUpModule';

import auth from '../utils/auth';


const jumbotronInstance = (
  <Jumbotron>
    <h1>Hello, world!</h1>
    <p>This is a simple hero unit, a simple jumbotron-style 
    component for calling extra attention to featured content or information.</p>
    <p><Button bsStyle="primary">Learn more</Button></p>
  </Jumbotron>
);


const gridInstance = (
  <Grid>   
    <Row className="show-grid">
      <Col xs={6} md={4}>Section 1</Col>
      <Col xs={6} md={4}>Section 1</Col>
      <Col xsHidden md={4}>Section 1</Col>
    </Row>
  </Grid>
);


export default class LandingPage extends React.Component {
    
  constructor(props) {
    super(props);

    this.state = {
      authenticated : auth.loggedIn()
    }
  }

  render() {

    var {authenticated} = this.state;

    return (
      <div>
        <TopNavBar authenticated={false} />
        <div className="container">
        </div>
      </div>      
    );
  }
};

LandingPage.propTypes = {
};



