import React from 'react';
import LoginFormModule from '../components/LoginFormModule';
import TopNavBar from '../components/TopNavBar';

import auth from '../utils/auth';

import UserActions from '../actions/UserActions';
import UserStore from '../stores/UserStore';

export default class Login extends React.Component {
  
  constructor(props,context) {
    super(props,context);

    this._onUserChangeEvent = this._onUserChangeEvent.bind(this);

    this.state = {
      authenticated : auth.loggedIn(),
      errorMsg : ''
    };

  }

  componentWillMount() {
    UserStore.onChange(this._onUserChangeEvent);
  }

  componentWillUnmount() {
    UserStore.offChange(this._onUserChangeEvent);
  }

  _onUserChangeEvent(){
    var userdata = UserStore.getData();
    var state = UserStore.getState();

    if( userdata ){
      if( userdata.in_setup == null || userdata.in_setup == true){
        this.context.router.replace('signup');
      }else{
        this.context.router.replace('/');
      }
    }else{
      this.setState({
        errorMsg : state.errorMsg
      });
    }
    
  }

  render() {

    var state = UserStore.getState();

    console.log(state);

    return (
      <div>
        <TopNavBar selectedTab='login' authenticated={this.state.authenticated} />
        <div className="container">
            <div className="row">
              <h1 className="col-md-5 col-md-offset-3" style={{padding:"30px 0px"}}>Log into your account</h1>
            </div>
            <div className="row">
                <div className="col-md-5 col-md-offset-3" style={{color:"red",fontWeight:"bold"}}>{this.state.errorMsg}</div>
            </div>
        </div>
        <LoginFormModule />
      </div>      
    );
  }
}

Login.contextTypes = {
  router: React.PropTypes.object,
};


