import React from 'react';

import TopNavBar from '../components/TopNavBar';
import Table from 'react-bootstrap/lib/Table';

import UserStore from '../stores/UserStore';
import NumbersStore from '../stores/NumbersStore';

import UserActions from '../actions/UserActions';
import NumbersActions from '../actions/NumbersActions';

import auth from '../utils/auth';

export default class Main extends React.Component {
  
  constructor(props) {
    super(props);

    this._onUserChangeEvent = this._onUserChangeEvent.bind(this);
    this._onNumbersChangeEvent =  this._onNumbersChangeEvent.bind(this);

    this.state = {
    	authenticated : auth.loggedIn(),
      userData : UserStore.getData(),
      numbers : NumbersStore.getNumbers()
    }
  }

  componentWillMount() {
    
  }

  componentDidMount() {
    UserStore.onChange(this._onUserChangeEvent);
    NumbersStore.onChange(this._onNumbersChangeEvent);
  }

  componentWillUnmount() {
    UserStore.offChange(this._onUserChangeEvent);
    NumbersStore.offChange(this._onNumbersChangeEvent);
  }

  _onUserChangeEvent(){
    this.setState({
      userData : UserStore.getData()
    });
  }

  _onNumbersChangeEvent(){
    this.setState({
      numbers : NumbersStore.getNumbers()
    });
  }

  render() {

    console.log('render');

    var userData = UserStore.getData();
    var numbers = NumbersStore.getNumbers();

    var calucroNumber = ( numbers.length )? numbers[0].number : null;
    var {first_name,last_name,email,company,phone} = userData;

    return (
      <div>
      	<TopNavBar selectedTab='dashboard' authenticated={this.state.authenticated} />
        <div className='container'>          
          <Table className='col-md-11 col-md-offset-1 ' striped bordered condensed hover>
            <thead>
              <tr>
                <th>First Name</th>
                <th>Last Name</th>
                <th>Email</th>
                <th>Company</th>
                <th>Phone Number</th>
                <th>Calucro Number</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{first_name}</td>
                <td>{last_name}</td>
                <td>{email}</td>
                <td>{company}</td>
                <td>{phone}</td>
                <td>{calucroNumber}</td>
              </tr>
            </tbody>
          </Table>
        </div>
      </div>
    );
  }
}

Main.propTypes = {
  
};


