

module.exports = {

  getToken() {
    return localStorage.token
  },

  logout(cb) {
    delete localStorage.token;
    delete localStorage.email;
    delete localStorage.user;
    delete localStorage.contacts;

    if (cb) cb()
    this.onChange(false)
  },

  loggedIn() {
    return !!localStorage.token && !!localStorage.email;
  },

  onChange() {}
};




