import React from 'react';
import ReactDOM from 'react-dom';
import { IndexRoute, Router, Route, hashHistory } from 'react-router';

import App  from  './App';

import Login        from './views/Login';
import LandingPage  from './views/LandingPage';
import Main         from './views/Main';

import SignUp       from './views/SignUp';
import SignUpFormUserCreate   from './views/SignUpFormUserCreate';
import SignUpFormUserInfo     from './views/SignUpFormUserInfo';
import SignUpFormChooseNumber from './views/SignUpFormChooseNumber';

import {requireAuth,routeSignUp} from  './utils/route-validations';

class WebContainer extends React.Component {

  constructor(props, context){
    super(props);
  }

  componentDidMount(){

  }

  render() {

    return (
      <Router history={hashHistory}>

        <Route path="/" component={App}>

          <IndexRoute component={Main} onEnter={requireAuth}></IndexRoute>

          <Route path="welcome" component={LandingPage}></Route>

          <Route path="login" component={Login} ></Route>

          <Route path="signup" component={SignUp} >
            
            <IndexRoute component={SignUpFormUserCreate}></IndexRoute>
            
            <Route path="2" component={SignUpFormUserInfo}></Route>
            <Route path="3" component={SignUpFormChooseNumber}></Route>
          </Route>

        </Route>

      </Router>
    );
  }
}

export default WebContainer;



