import React from 'react';
import form from 'react-bootstrap/lib/form';
import FormGroup from 'react-bootstrap/lib/FormGroup';
import InputGroup from 'react-bootstrap/lib/InputGroup';
import ControlLabel from 'react-bootstrap/lib/ControlLabel';
import FormControl from 'react-bootstrap/lib/FormControl';
import HelpBlock from 'react-bootstrap/lib/HelpBlock';
import Button from 'react-bootstrap/lib/Button';

import UserActions from '../actions/UserActions';
import UserStore from '../stores/UserStore';

function validateEmail(email) {
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
}

const buttonStateText = ['Create Account','Creating Acount..', 'Try again later!'];

export default class SignUpFormModule extends React.Component {
  
  constructor(props) {

    super(props);

    this.getPasswordValidationState = this.getPasswordValidationState.bind(this);
    this.handleChangeEmail = this.handleChangeEmail.bind(this);
    this.handleChangePassword = this.handleChangePassword.bind(this);
    this.handleConfirmPassword = this.handleConfirmPassword.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this._onUserChangeEvent = this._onUserChangeEvent.bind(this);

    this.state = {
      email:    '',
      password: '',
      confirm:  '',
      ready :    false,
      loading : false,
      done : false
    };
    
  }
  
  componentWillMount() {
    UserStore.onChange(this._onUserChangeEvent);
  }

  componentWillUnmount() {
    UserStore.offChange(this._onUserChangeEvent);
  }

  _onUserChangeEvent(){

    // var user = UserStore.getState();

    // this.setState({
    //   loading: false
    // });

    // console.log(user);

  }

  getPasswordValidationState() {
    const length = this.state.password.length;
    if (length > 7) return 'success';
    else if (length > 5) return 'warning';
    else if (length > 0) return 'error';
  }

  getConfirmValidationState() {
    if(this.state.password.length == 0) return;
    const length = this.state.password;
    if (this.state.password == this.state.confirm) return 'success';
    else return 'error';
  }

  handleChangeEmail(e) {

    var ready = false;

    if( (validateEmail(this.state.email)) && (this.state.confirm == this.state.password)
        && (this.state.password.length > 7 ) )
       ready = true;

    this.setState({ email: e.target.value, ready: ready});
  }

  handleChangePassword(e) {
    var ready = false;

    if( (validateEmail(this.state.email)) && (this.state.confirm == e.target.value)
        && (this.state.password.length > 7 ) )
       ready = true;

    this.setState({ password: e.target.value, ready: ready });
  }

  handleConfirmPassword(e) {
    var ready = false;

    if( (validateEmail(this.state.email)) && (this.state.password == e.target.value)
        && (this.state.password.length > 7 ) )
       ready = true;

    this.setState({ confirm: e.target.value, ready: ready });
  }

  onSubmit(){

    var {email,password} = this.state;

    if( this.state.ready ){
      
      this.setState({
        loading: true
      });

      UserActions.create(null,null,email,password);
    }
    
  }

  render() {

    var buttonText = ( this.state.loading ) ? buttonStateText[1] : buttonStateText[0];

    return (
      <div className="container">
       
          <div className="col-md-6 col-md-offset-3">
          <h1 style={{padding:"30px 0px"}}>Create your account</h1>
            <form>
              <FormGroup>
                <InputGroup>
                  <InputGroup.Addon>Email Address</InputGroup.Addon>
                  <FormControl
                    type="text"
                    ref="email"
                    value={this.state.email}
                    placeholder="( Valid email address required )"
                    onChange={this.handleChangeEmail}
                  />
                </InputGroup>
              </FormGroup>

              <FormGroup
                controlId="formPass"
                validationState={this.getPasswordValidationState()} >
                <InputGroup>
                  <InputGroup.Addon>Password</InputGroup.Addon>
                  <FormControl
                    type="password"
                    ref="password"
                    value={this.state.password}
                    placeholder="Enter password"
                    onChange={this.handleChangePassword}
                  />
                </InputGroup>
              </FormGroup>

                <FormGroup
                  controlId="formPass"
                  validationState={this.getConfirmValidationState()}>
                  <InputGroup>
                    <InputGroup.Addon>Confirm</InputGroup.Addon>
                    <FormControl
                      type="password"
                      ref="confirm"
                      value={this.state.confirm}
                      placeholder="Confirm password"
                      onChange={this.handleConfirmPassword}
                    />
                  </InputGroup>
                </FormGroup>
                <FormControl.Feedback /><br/>
              <Button className="col-md-4" bsStyle="success" disabled={!this.state.ready} onClick={this.onSubmit}>{buttonText}</Button>
            </form>
            
        </div>
      </div>
    );
  }
}

SignUpFormModule.propTypes = {
  onCreateUser : React.PropTypes.func.isRequired
};


