import React from 'react';
import UserStore from '../stores/UserStore';

import auth from '../utils/auth';

export default class SignUp extends React.Component {
  
  constructor(props, context) {
    super(props, context);

    this.routeSignUp = this.routeSignUp.bind(this);
    this._onUserChangeEvent = this._onUserChangeEvent.bind(this);
  }

  componentDidMount(){
   
  }

  componentWillUnmount(){
    UserStore.offChange(this._onUserChangeEvent);
  }

  componentWillMount(){
    
    UserStore.onChange(this._onUserChangeEvent);

    var userstate = UserStore.getState();

    if( auth.loggedIn()){
      console.log("User logged in!", userstate);
      this.routeSignUp();
    }else{ 
      // Log user out
      auth.logout();
    } 
  }    
  

  routeSignUp(){

    var userstate = UserStore.getState()

    var {data} = userstate;

    if( data ){
      if( data.in_setup == false ){      
        this.context.router.replace("/");
      }else if( data.in_setup && !(data.first_name && data.last_name && data.phone) ){
        console.log("User needs to update registration info!");
        this.context.router.replace("signup/2");
      }else if( data.in_setup && data.first_name && data.last_name && data.phone ){
        this.context.router.replace("signup/3");
        console.log("User needs to get a Calucro Number!");
      }
    }

  }

  _onUserChangeEvent(){

    var userstate = UserStore.getState()

    var {data} = userstate;

    if( auth.loggedIn()){
      console.log("User logged in!", userstate);
      this.routeSignUp();
    }else{ 
      // Log user out
      auth.logout();
    } 
  }


  render() {
    return (
      <div style={{height:window.innerHeight-60}}>
        {this.props.children}
      </div>
    );
  }
}

SignUp.contextTypes = {
  router: React.PropTypes.object,
};



