import ReactFlux from 'react-flux';
import NumbersConstants from '../constants/NumbersConstants';
import UserActions from '../actions/UserActions';

var NumbersStore = ReactFlux.createStore({

	getInitialState: function(){
		return {
			numbers: [],
			searchNumbers : [],
			error: null
		}
	},

	storeDidMount: function(){
		// console.log("NumbersStore.storeDidMount");
	},

	getSearchNumbers(){
		return this.get('searchNumbers');
	},

	getNumbers(){
		return this.get('numbers');
	},

	getStateString(){
		return JSON.stringify(this.getState());
	}

}, 
	[	

		[NumbersConstants.GET, function onGet(payload){
			console.log("NumbersStore.onGet", payload);
		}],

		[NumbersConstants.GET_SUCCESS, function handleGetSuccess(payload){

			console.log("NumbersStore.handleGetSuccess", payload);
			
			if( payload.length > 0 ){			
				this.setState({
					isUpdating: false,
					numbers: payload,
					error: null
				});
			}			
		}],

		[NumbersConstants.GET_FAIL, function handleGetFail(payload){

			console.log("NumbersStore.handleGetFail", payload);
					
		}],


		[NumbersConstants.SEARCH, function onGet(payload){
			console.log("NumbersStore.onGet", payload);
			this.setState({
				isUpdating: true
			});
		}],

		[NumbersConstants.SEARCH_SUCCESS, function handleGetSuccess(payload){
			
			if( payload.length > 0 ){			
				this.setState({
					isUpdating: false,
					searchNumbers: payload,
					error: null
				});
			}
			
		}],

		[NumbersConstants.CREATE, function onCreate(payload){
			
			console.log("NumbersStore.onCreate", payload);
			
			this.setState({
				isUpdating: true
			});
		}],

		[NumbersConstants.CREATE_SUCCESS, function handleCreateSuccess(payload){
			
			var numbers = this.get('numbers');

			console.log("NumbersStore.handleCreateSuccess", payload);
			
			if( payload.id ){
				numbers.push(payload);
				this.setState({
					isUpdating: false,
					numbers: numbers,
					error: null
				});
				UserActions.get();
			}
		}]
	]
);

module.exports = NumbersStore;


