import React from 'react';

import form from 'react-bootstrap/lib/form';
import FormGroup from 'react-bootstrap/lib/FormGroup';
import InputGroup from 'react-bootstrap/lib/InputGroup';
import ControlLabel from 'react-bootstrap/lib/ControlLabel';
import FormControl from 'react-bootstrap/lib/FormControl';
import HelpBlock from 'react-bootstrap/lib/HelpBlock';
import Button from 'react-bootstrap/lib/Button';

import TopNavBar from '../components/TopNavBar';

import UserStore from '../stores/UserStore';
import UserActions  from '../actions/UserActions';

import auth from '../utils/auth';

export default class SignUpFormUserInfo extends React.Component {
  
  constructor(props,context) {
    super(props,context);

    this.handleFirstChange = this.handleFirstChange.bind(this);
    this.handleLastChange = this.handleLastChange.bind(this);
    this.handlePhoneChange = this.handlePhoneChange.bind(this);
    this.handleCompanyChange = this.handleCompanyChange.bind(this);
    this.getPhoneNumberValidationState = this.getPhoneNumberValidationState.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this._onUserChangeEvent = this._onUserChangeEvent.bind(this);


    var userstate = UserStore.getState();
    var userData = userstate.data;

    this.state = {
    	authenticated : auth.loggedIn(),
      first : userData.first_name || '',
      last : userData.last_name || '',
      phone : userData.phone || '',
      company : userData.company || '',
      ready:(userData.first_name && userData.last_name && userData.phone)
    };

  }

  componentWillMount() {
    UserStore.onChange(this._onUserChangeEvent);
  }

  componentWillUnmount() {
    UserStore.offChange(this._onUserChangeEvent);
  }

  getPhoneNumberValidationState() {
    const length = this.state.phone.length;
    if (length > 9) return 'success';
    else if (length > 0) return 'error';
  }

  handleFirstChange(e) {
    var ready = false;

    if( e.target.value.length > 0  && this.state.last.length > 0 && this.state.phone.length > 9 ){
        ready = true;
    }

    this.setState({ first: e.target.value, ready: ready});
  }

  handleLastChange(e) {

    var ready = false;

    if( this.state.first.length > 0  && e.target.value.length > 0 && this.state.phone.length > 9 ){
        ready = true;
    }

    this.setState({ last: e.target.value, ready: ready});
  }

  handlePhoneChange(e) {
    var ready = false;

    if( this.state.first.length > 0  && this.state.last.length > 0 && e.target.value.length > 9 ){
        ready = true;
    }

    this.setState({ phone: e.target.value, ready: ready});
  }

  handleCompanyChange(e) {
    this.setState({ company: e.target.value });
  }

  onSubmit(){

    var userstate = UserStore.getState();
    var userData = userstate.data;

    var formData = {
      first_name: this.state.first,
      last_name: this.state.last,
      phone: this.state.phone,
      company: this.state.company
    };

    $.extend(userData,formData);

    UserActions.update(userData);
  }


  _onUserChangeEvent(){
    // var userData = UserStore.getData();

    // if( userData.first_name && userData.last_name && userData.phone ){
    //   this.context.router.replace("signup/3");
    // }
  }

  render() {

    return (
      <div>
        <TopNavBar authenticated={this.state.authenticated} />
        <div className="container">       
          <div className="col-md-9 col-md-offset-2">
	          <h1 style={{padding:"30px 0px"}}>Welcome to Calucro!</h1>
	          <p>Please fill out the form below to complete registration</p>
            <br />
	          <form>	
              <div className="row">        
                <div className="col-md-6">
                  <InputGroup>
                    <InputGroup.Addon>First Name</InputGroup.Addon>
                    <FormControl
                      type="text"
                      tabIndex="1"
                      ref="first"
                      value={this.state.first}
                      placeholder=""
                      onChange={this.handleFirstChange}
                    />
                  </InputGroup>
                  <br/>
                  <FormGroup validationState={this.getPhoneNumberValidationState()}>
                    <InputGroup>
                      <InputGroup.Addon>Phone Number</InputGroup.Addon>
                      <FormControl

                        type="text"
                        tabIndex="3"
                        ref="phone"
                        value={this.state.phone}
                        placeholder=""
                        onChange={this.handlePhoneChange}
                      />
                    </InputGroup>
                  </FormGroup>
                </div>

                <FormGroup className="col-md-6" >
                <InputGroup>
                    <InputGroup.Addon>Last Name</InputGroup.Addon>
                    <FormControl
                      type="text"
                      tabIndex="2"
                      ref="last"
                      value={this.state.last}
                      placeholder=""
                      onChange={this.handleLastChange}
                    />
                  </InputGroup>
                  
                  <br/>
                  <InputGroup>
                    <InputGroup.Addon>Company</InputGroup.Addon>
                    <FormControl
                      type="text"
                      tabIndex="4"
                      ref="company"
                      value={this.state.company}
                      placeholder="( optional )"
                      onChange={this.handleCompanyChange}
                    />
                  </InputGroup>
                </FormGroup>
              </div>
              <Button className="col-md-2 pull-right" bsStyle="success" disabled={!this.state.ready} onClick={this.onSubmit}>Next</Button>
	          </form>
          </div>
      </div>
     </div>      
    );
  }
}

SignUpFormUserInfo.propTypes = {
};

SignUpFormUserInfo.contextTypes = {
  router : React.PropTypes.object
};



