import ReactFlux from 'react-flux';
import UserConstants from '../constants/UserConstants';
import Api from '../utils/Api';


var UserStore = ReactFlux.createStore({

	getInitialState: function(){
				
		if( localStorage.user ){
			console.log("UserStore.getInitialState()","Loading prevState");
			let prevState = JSON.parse(localStorage.user);
			return prevState;
		}

		return {
			data: null,
			isAuth: localStorage.token ? true : false,
			error: null
		}
	},

	storeDidMount: function(){
		// console.log("UserStore.storeDidMount");
	},

	getStateString(){
		return JSON.stringify(this.getState());
	},

	getData(){
		return this.get('data');
	},

	getEmail: function(){
		return this.get('isAuth') ? this.get('data').email : null;
	},

	isVerified: function(){
		return this.get('isAuth') ? this.get('data').phone_verified : false;
	},

	persist : function(){
		let state = JSON.stringify(this.getState());
		localStorage.user = state;
	}

}, [

	[UserConstants.GET, function onGet(payload){
		console.log("UserStore.onGet", payload);
	}],

	[UserConstants.GET_SUCCESS, function handleGetSuccess(payload){
		console.log("UserStore.handleGetSuccess", JSON.stringify(payload));
		if( payload.id ){			
			this.setState({
				isUpdating: false,
				data: payload,
				isAuth : true,
				error: null
			});
			this.persist();
		}
		
	}],

	[UserConstants.GET_FAIL, function handleGetFailure(error){
		console.log("UserStore.handleGetFailure", error);
		this.setState({
			isUpdating: false,
			data: null,
			error: error.message
		});
	}],

	[UserConstants.UPDATE, function onUpdate(payload){
		console.log("UserStore.onUpdate", payload);
		this.setState({
			isUpdating: true
		});
	}],

	[UserConstants.UPDATE_SUCCESS, function handleUpdateSuccess(payload){
		console.log("UserStore.handleUpdateSuccess", payload);

		if( payload.id ){
			this.setState({
				isUpdating: false,
				data: payload,
				isAuth : true,
				error: null
			});
			this.persist();
		}
		
	}],

	[UserConstants.UPDATE_FAIL, function handleUpdateFailure(error){
		console.log("UserStore.handleUpdateFailure", error);
		this.setState({
			isUpdating: false,
			data: null,
			error: error.message
		});
	}],


	[UserConstants.CREATE, function onCreate(payload){
		console.log("UserStore.onCreate", payload);
		this.setState({
			isLoggingIn: true,
			error:null,
			errorMsg:null
		});
	}],


	[UserConstants.CREATE_SUCCESS, function handleCreateSuccess(payload){
		
		if(payload){
			if(payload.body){
				if(payload.body.error){
					console.log("UserStore.handleCreate", payload);

					this.setState({
						error: true,
						errorMsg: payload.body.error,
						isAuth: false,
						isLoggingIn: false
					});

					return;
				}
			}
		}
		
		this.setState({
			isLoggingIn: false,
			error: null,
			data: payload,
			isAuth: true
		});
		this.persist();
	}],
	

	[UserConstants.CREATE_FAIL, function handleCreateFailure(error){
		console.log("UserStore.handleCreateFailure", error);
		this.setState({
			isLoggingIn: false,
			error: error.message
		});
	}],


	/**
	* Dispatcher calls this directly when it receives a USER_LOGIN message,
	* just before it tries to execute the corresponding action
	*/
	[UserConstants.LOGIN, function onLogin(payload){
		console.log("UserStore.onLogin", JSON.stringify(payload));
		
	}],

	[UserConstants.LOGIN_SUCCESS, function handleLoginSuccess(payload){
		
		console.log("UserStore.handleLoginSuccess");
		console.log(payload);
		
		let { body } = payload;

		if( body ){
			this.setState({
				isLoggingIn: false,
				error:true,
				data:null,
				errorMsg: body.error
			});
			return;
		}

		this.setState({
			isLoggingIn: false,
			error: null,
			data: payload,
			isAuth: true
		});

		this.persist();
	}],


	[UserConstants.LOGIN_FAIL, function handleLoginFailure(payload){
		console.log("UserStore.handleLoginFailure()");
		console.log(JSON.stringify(payload));
		this.setState({
			isLoggingIn: false,
			error:true,
			errorMsg: payload.error
		});
	}]
]);

UserStore.addActionHandler(UserConstants.LOGOUT, {
	success: function(){
		console.log("UserStore.handleLogout");
		delete localStorage.token;
		this.parent.setState({
			isAuth: false,
			data: null
		});
	}
});

UserStore.addActionHandler(UserConstants.IMPORT_SMS, {
	success: function(payload){
		console.log("UserStore.importSMSSuccess : " + JSON.stringify(payload));
		
		this.setState({
			res : payload
		});
	}
});

UserStore.addActionHandler(UserConstants.REQUEST_PHONE_VERIFICATION, {
	//returns initial state specific only to this handler
    getInitialState: function(){
    	return{
	        isAwaitingVerification: false,
	        requestCodeSent : false
	    }
    },
    before: function(){
		this.setState({
			isAwaitingVerification: true,
		});
	},
	after: function(){
		this.setState({
			isAwaitingVerification: false,
		});
	},
	success: function(payload){

		this.setState({
			isAwaitingVerification: false,
			requestCodeSent : true			
		});

		this.parent.setState({
			data : payload
		});

		this.parent.persist();
	}
});

UserStore.addActionHandler(UserConstants.USER_REPORT, {
	//returns initial state specific only to this handler
    getInitialState: function(){
        return {
	        fetching: false
	    }
    },
	before: function(){
		this.setState({
			fetching: true,
		});
	},
	after: function(){
		this.setState({
			fetching: false,
		});
	},
	success: function(payload){

		this.setState({
			data:payload
		});
		this.parent.persist();
	}
});

UserStore.addActionHandler(UserConstants.SUBMIT_PHONE_VERIFICATION, {
	//returns initial state specific only to this handler
    getInitialState: function(){
        return {
	        isAwaitingVerification: false
	    }
    },
	before: function(){
		this.setState({
			isAwaitingVerification: true,
			error: false,
			errorMsg: null
		});
	},
	after: function(payload){

		if( payload ){
			if(payload.body){
				if(payload.body.error){
					this.setState({
						error: true,
						isAwaitingVerification: false,
						errorMsg: payload.body.error
					});
					return;
				}
			}
		}

		this.setState({
			isAwaitingVerification: false,
			error: false,
			errorMsg: null
		});
	},

	success: function(payload){

		this.parent.setState({
			data:payload
		});
		this.parent.persist();
	}
});

module.exports = UserStore;


