import React from 'react';

import form from 'react-bootstrap/lib/form';
import FormGroup from 'react-bootstrap/lib/FormGroup';
import InputGroup from 'react-bootstrap/lib/InputGroup';
import ControlLabel from 'react-bootstrap/lib/ControlLabel';
import FormControl from 'react-bootstrap/lib/FormControl';
import Button from 'react-bootstrap/lib/Button';
import ListGroup from 'react-bootstrap/lib/ListGroup';
import ListGroupItem from 'react-bootstrap/lib/ListGroupItem';

import BeatLoader from 'respinner/lib/BeatLoader'

import TopNavBar from '../components/TopNavBar';

import UserStore from '../stores/UserStore';
import UserActions  from '../actions/UserActions';
import NumbersActions from '../actions/NumbersActions';
import NumbersStore from '../stores/NumbersStore';

import auth from '../utils/auth';

const beatLoaderInstance  = (
  <div style={{padding:"100px 0px"}}>
    <BeatLoader className="col-md-6 col-md-offset-4 " fill="#333" count={8} />
  </div>
);

export default class SignUpFormChooseNumber extends React.Component {
  
  constructor(props,context) {
    super(props,context);

    this._onReceivedNumbers = this._onReceivedNumbers.bind(this);
    this._onStatusChange = this._onStatusChange.bind(this);
    this.onListItemTap = this.onListItemTap.bind(this);
    this.onSubmit = this.onSubmit.bind(this);

    this.state = {
      authenticated : auth.loggedIn(),
      loading : false,
      selectedNumberIndex : -1,
      numbers : [],
      startPurchase : false
    };

  }

  componentWillMount(){

    var userState = UserStore.getState();
    var userData = userState.data;

    if( userData.phone ){
      NumbersActions.search( userData.phone , 5);
      this.setState({
        loading : true
      });
    }
  }

  componentDidMount() {
    NumbersStore.onChange(this._onReceivedNumbers);
    UserStore.onChange(this._onStatusChange);
  }

  componentWillUnmount() {
    NumbersStore.offChange(this._onReceivedNumbers);
    UserStore.offChange(this._onStatusChange);
  }

  _onReceivedNumbers(){

    if( this.state.startPurchase && this.state.selectedNumberIndex ){

      this.setState({
        loading: true
      });

    }else{

      var calucroNumbers = NumbersStore.getSearchNumbers();

      if( calucroNumbers.length ){
        this.setState({
          numbers : calucroNumbers,
          loading: false
        });
      }
    }    
  }

  _onStatusChange(){

    var userdata = UserStore.getData();

    if( userdata ){
      if( !userdata.in_setup ){
        this.context.router.replace("/");
      }
    }
  }

  onListItemTap(e){
    this.setState({
      selectedNumberIndex: e.target.value
    });
  }

  onSubmit(e){

    var selected = this.state.numbers[this.state.selectedNumberIndex];
    console.log(selected);

    if( selected.number ){
      this.setState({
        startPurchase : true,
        loading : true
      });
      NumbersActions.create(selected.number);
    }

  }

  render() {

    var _self = this;
    
    var buildList = function(calucroNumber,x){      
      return (
        <ListGroupItem 
          key={x}
          value={x}
          style={{outline:"none"}} 
          onClick={_self.onListItemTap}>
            <span>{calucroNumber.number}</span>
            <span className="pull-right">{calucroNumber.city + ", " + calucroNumber.state}</span>
        </ListGroupItem>
      );
    };

    var buildSelectedList = function(calucroNumber,x){
     
      if( x == _self.state.selectedNumberIndex ){
        return (
          <ListGroupItem 
            key={x}
            value={x} 
            style={{outline:"none"}} 
            onClick={_self.onListItemTap}>
              <span>{calucroNumber.number}</span>
              <span className="pull-right">{calucroNumber.city + ", " + calucroNumber.state}</span>
          </ListGroupItem>
        );
      }      
    }

    var prePurchaseInterface = (
      <div className="col-md-6 col-md-offset-3">
        <h1 style={{padding:"30px 0px"}}>Select your Calucro Number</h1>
        <p>Choose a number from the list of numbers below.</p>
        <br />  
            <div className="row">        
              <div className="col-md-12">
                 <ListGroup>
                  {(this.state.loading) ? beatLoaderInstance : this.state.numbers.map(buildList)}
                 </ListGroup>
              </div>
            </div>
            <Button 
              className={(this.state.numbers.length) ? "col-md-3 pull-right" : "hidden" }
              bsStyle="success"
              disabled={!(this.state.selectedNumberIndex > -1)} 
              onClick={this.onSubmit}>
              Get Number
            </Button>
       </div>
    );

    var purchaseInterface = (
      <div className="col-md-6 col-md-offset-3">
        <h1 style={{padding:"30px 0px"}}>Select your Calucro Number</h1>
        <p>Choose a number from the list of numbers below.</p>
        <br />  
            <div className="row">        
              <div className="col-md-12">
                 <ListGroup>
                  {(this.state.loading) ? beatLoaderInstance : this.state.numbers.map(buildSelectedList)}
                 </ListGroup>
              </div>
            </div>
            <Button 
              className={(this.state.numbers.length) ? "col-md-2 pull-right" : "hidden" }
              bsStyle="success"
              disabled={!(this.state.selectedNumberIndex > -1)} 
              onClick={this.onSubmit}>
              Next
            </Button>
       </div>
    );

    return (
      <div>
        <TopNavBar authenticated={this.state.authenticated} />
        <div className="container">       
          {(this.state.startPurchase) ? purchaseInterface : prePurchaseInterface}
         </div>
      </div>
    );
  }
}

SignUpFormChooseNumber.propTypes = {
  name: React.PropTypes.string,
};


SignUpFormChooseNumber.contextTypes = {
  router: React.PropTypes.object,
};


