import React from 'react';
import Nav from 'react-bootstrap/lib/Nav';
import Navbar from 'react-bootstrap/lib/Navbar';
import NavItem from 'react-bootstrap/lib/NavItem';
import NavDropdown from 'react-bootstrap/lib/NavDropdown';
import MenuItem from 'react-bootstrap/lib/MenuItem';

import auth from '../utils/auth';

export default class TopNavBar extends React.Component {
  
  constructor(props,context) {
    super(props,context);

    this.onLoginClick = this.onLoginClick.bind(this);
    this.onLogoutClick = this.onLogoutClick.bind(this);
    this.onSignupClick = this.onSignupClick.bind(this);

    this.tabs = {
      'login': 1,
      'signup': 2
    };

  }

  onLoginClick(e){
    console.log(e);
    this.context.router.replace("login");

  }

  onLogoutClick(e){
    auth.logout();
    this.context.router.replace("login");
  }

  onSignupClick(e){
    console.log(e);
    this.context.router.replace("signup");
  }

  render() {

    var {authenticated} = this.props;
    var navigationItems,activeTab = 0;

    if( this.props.selectedTab ){
      activeTab = this.tabs[this.props.selectedTab]
    }

    if( authenticated ){
      navigationItems = (
        <Nav activeKey={activeTab} pullRight>
            <NavItem eventKey={1} >My Account</NavItem>
            <NavItem onClick={this.onLogoutClick} eventKey={2} href="signup">Logout</NavItem>
          </Nav>
      );
    }
    else{
      navigationItems = (
        <Nav activeKey={activeTab} pullRight>
            <NavItem onClick={this.onLoginClick} eventKey={1} href="login">Login</NavItem>
            <NavItem onClick={this.onSignupClick} eventKey={2} href="signup">Create an Account</NavItem>
          </Nav>
      );
    }

    return (
      <Navbar inverse collapseOnSelect>
        <Navbar.Header>
          <Navbar.Brand>
            <a href="#">Calucro</a>
          </Navbar.Brand>
          <Navbar.Toggle />
        </Navbar.Header>
        <Navbar.Collapse>
          {navigationItems}
        </Navbar.Collapse>
      </Navbar>
    );
  }
}

TopNavBar.propTypes = {
  selectedTab : React.PropTypes.string
};

TopNavBar.contextTypes = {
  router: React.PropTypes.object
}


