import ReactFlux from 'react-flux';
import UserConstants from '../constants/UserConstants.js'
import Api from '../utils/Api';
import auth from '../utils/auth';


var UserActions = ReactFlux.createActions({

	// Dispatch Login Action
    login: [UserConstants.LOGIN, function loginAction(username, password){
        //console.log("UserActions.login()");
        // Make API CALL
        return Api.login(username,password);

    }],

    // Dispatch Login Action
    create: [UserConstants.CREATE, function signUpAction(first,last, email, password){
		//console.log("UserActions.create()",[fullname,email,password]);
        // Make API CALL
        return Api.signUp(first,last,email,password);

    }],

    get : [UserConstants.GET, function getAction(){
		if( auth.loggedIn() )
            return Api.getUser();
    }],

    update: [UserConstants.UPDATE, function update(payload){
        // Make API CALL
        if( auth.loggedIn() )
            return Api.updateUser(payload);

    }],

    verifyPhone : [UserConstants.REQUEST_PHONE_VERIFICATION, function verifyPhoneAction(phone){
		//console.log("UserActions.verifyPhoneAction()",localStorage.token);
        // Make API CALL
        if( auth.loggedIn() )
            return Api.requestPhoneVerification(phone);
    }],

    submitVerifyCode : [UserConstants.SUBMIT_PHONE_VERIFICATION, function submitVerifyCode(code){
		//console.log("UserActions.submitVerifyCode()",localStorage.token);
        // Make API CALL
        if( auth.loggedIn() )
            return Api.submitPhoneVerification(code);
    }],

    getUserReport : [UserConstants.USER_REPORT, function getUserReport(code){
        //console.log("UserActions.getUserReport()",localStorage.token);
        // Make API CALL
        if( auth.loggedIn() )
            return Api.getUserReport(code);
    }],

    importSMS: [UserConstants.IMPORT_SMS, function importSMS(payload){
        //console.log("UserActions.importSMS");
        //console.log(JSON.stringify(payload));
        
        // Make API CALL
        if( auth.loggedIn() )
            return Api.importSMS(payload);

    }],

});

module.exports = UserActions;


